import "core-js/modules/es.array.push.js";
import { useStore } from 'vuex';
import myEventBus from '@/assets/js/bus';
import { ref, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'searchheader',
  setup() {
    const router = useRouter();
    const emitter = myEventBus();
    const store = useStore();
    const inputval = ref('');
    const toup = () => {
      router.go(-1);
    };
    const cleaript = () => {
      inputval.value = '';
      emitter.emit('clear', false);
    };
    const tosearchclick = () => {
      if (inputval.value.trim() === '') {
        return false;
      }
      emitter.emit('changeshow', false);
      emitter.emit('scrollimg', false);
      setTimeout(() => {
        emitter.emit('values', inputval.value);
      }, 500);
    };
    const tochangeCity = () => {
      router.push('/citys');
    };
    const deleteclick = () => {
      if (inputval.value.trim() === '') {
        emitter.emit('clear', false);
      }
    };
    const close = computed(() => {
      if (inputval.value === '') {
        return false;
      } else {
        return true;
      }
    });
    const sorestate = computed(() => store.state.city);
    onMounted(() => {
      emitter.on('changeinpt', title => {
        inputval.value = title;
      });
    });
    return {
      inputval,
      toup,
      cleaript,
      tosearchclick,
      deleteclick,
      sorestate,
      close,
      tochangeCity
    };
  }
};